<template>
  <div class="user-body">
    <div class="user-header">
      <a class="nav-item1" @click="backHoom"><img class="game_1" src="../../assets/images/usercenter/game_1.png"/></a>
      <a class="gucang">谷仓</a>
      <a class="nav-item2" @click="mail"><img class="wh_38" src="../../assets/images/usercenter/wh_38.png"/>邮寄</a>
    </div>
    <div class="userInfoBar">
      <div class="userInfo" @click="personalInFo">
        <div class="customer">
          <div><img :src="userPath"/></div>
          <div class="userId">
            <a>{{userName}}</a>
          </div>
        </div>
        <img class="rightArrow" src="../../assets/images/usercenter/gc_28.png"/>
      </div>
    </div>
    <div class="userMain">
      <ul>
        <li @click="mydoll">
          <p>
            <img src="../../assets/images/usercenter/wh_39.png"/>
            <a>娃娃箱</a>
          </p>
          <img class="rightArrow" src="../../assets/images/usercenter/gc_28.png"/>
        </li>
        <li @click="myDiamond">
          <p>
            <img src="../../assets/images/usercenter/wh_40.png"/>
            <a>我的钻石</a>
          </p>
          <img class="rightArrow" src="../../assets/images/usercenter/gc_28.png"/>
        </li>
        <li @click="myPoints">
          <p>
            <img src="../../assets/images/usercenter/wh_41.png"/>
            <a>我的积分</a>
          </p>
          <img class="rightArrow" src="../../assets/images/usercenter/gc_28.png"/>
        </li>
        <li @click="prepaidRecords">
          <p>
            <img src="../../assets/images/usercenter/wh_42.png"/>
            <a>充值记录</a>
          </p>
          <img class="rightArrow" src="../../assets/images/usercenter/gc_28.png"/>
        </li>
      </ul>
    </div>
    <div @click="exitout" class="exit">
      退出登陆
    </div>
  </div>
</template>
<script>
  import nologin from '@/assets/images/home/nologin.png'
  import defalut from '@/assets/images/home/defalut.png'
  var urlhost = process.env.API_AJAX;
  function getCookie(name) {
    var v = window.document.cookie.match('(^|;) ?' + name + '=([^;]*)(;|$)');
    return v ? v[2] : null;
  }
  function setCookie(name, value, days) {
    var d = new Date;
    d.setTime(d.getTime() + 24*60*60*1000*days);
    window.document.cookie = name + "=" + value + ";path=/;expires=" + d.toGMTString();
  }
  export default {
    name:"gucang",
    data() {
      return {
        defalut:defalut,
        nologin:nologin,
        userName:null,
        nickname:"",
        userPath:null,
      }
    },
    components: {
    },
    mounted:function () {
      var _this= this;
      var userId = getCookie('userId');
      var token = getCookie("wawajitoken");
      // var userId = this.$store.state.userId;
      // var userId = null;
      // var token = null;
      if(token){
        $.ajax({
          type: 'GET',
          url: urlhost+"usercenter/userinfo?user_id="+userId,
          data$Type: "jsonp",
          data: {
          },
          success: function (response) {
            var data = JSON.parse(response).message;
              _this.userName = data.nickname;
              if(data.img_url == null || data.img_url == ''){
                _this.userPath = _this.defalut;
              }else{
                _this.userPath = data.img_url;
              }
          }
        });
      }else{
        _this.userName = getCookie("name");
        _this.userPath = _this.nologin;
      }
    },
    methods:{
      saveWwjtoken: function(token){
        this.$store.dispatch('saveWwjtoken', token);
      },
      backHoom:function () {
        this.$router.go(-1);
      },
      exitout:function () {
        var _this = this;
        var ua = window.navigator.userAgent.toLowerCase();
        if(ua.match(/MicroMessenger/i) == 'micromessenger'){
          _this.$confirm("确定退出吗").then(
            function () {
              setTimeout(function () {
                _this.clearAll();
                setCookie("wawajitoken","",-1);
                // _this.saveWwjtoken("");
                var lacoUrl = window.location.href.split("#")[0];
                window.location.href = urlhost + "user/oauth?url="+lacoUrl;
              },1000)

            }
          ).catch(
            function () {
              return
            }
          )
        }else{
          _this.$confirm("确定退出吗").then(
            function () {
              _this.clearAll();
              setCookie("wawajitoken","",-1);
              _this.$router.push({
                path:"/visit/login"
              })
            }
          ).catch(
            function () {
              return
            }
          )
        }


      },
      clearAll:function () {
        var keys = document.cookie.match(/[^ =;]+(?=\=)/g);
        if(keys) {
          for(var i = keys.length; i--;)
            setCookie(keys[i],"",-1);
        }
      },
      myPoints:function () {
        this.$router.push({path:"/usercenter/myPoints"})
      },
      mail:function () {
        this.$router.push({path:"/usercenter/mailOrder"})
      },
      personalInFo:function () {
        this.$router.push({path:"/usercenter/userInfo"})
      },
      mydoll:function () {
        this.$router.push({path:"/usercenter/mydoll"})
      },
      myDiamond:function () {
        this.$router.push({path:"/usercenter/mydiamond"})
      },
      prepaidRecords:function () {
        this.$router.push({path:"/usercenter/payrecords"})
      }
    },
    watch:{
      '$router': 'getParams'
    }
  }
</script>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
  .user-body{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    background-color: #f2f2f2;
    .user-header{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: n(80);
      background: #fff001;
      display: flex;
      justify-content: space-between;
      align-items: center;
      z-index: 999;
      border-bottom: 1px solid #d8d8d8;
      .nav-item1{
        .game_1{
          margin-left: n(23);
          width: n(50);
          height: n(50);
        }
      }
      .gucang{
        font-size: 14px;
        font-weight: bold;
      }
      .nav-item2{
        margin-right: n(23);
        .wh_38{
          width: n(50);
          height: n(50);
        }
      }
    }
    .userInfoBar{
      margin-top: n(80);
      width: 100%;
      height: n(300);
      background: url("https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/wh_48.png") no-repeat;
      background-size: 100% 100%;
      position: relative;
      .userInfo{
        position: absolute;
        top:n(150);
        left:5%;
        width: 90%;
        height: n(125);
        background: #ffffff;
        -webkit-border-radius: 10px;
        -moz-border-radius: 10px;
        border-radius: 10px;
        display: inline-flex;
        justify-content: space-between;
        align-items: center;
        .customer{
          margin-left: n(27);
          height: n(125);
          display: inline-flex;
          justify-content: center;
          align-items: center;
          div{
            img{
              -webkit-border-radius: 50%;
              -moz-border-radius: 50%;
              border-radius: 50%;
              border: 1px solid #d2eee2;
              width: n(85);
              height: n(85);
            }

          }
          .userId{
            margin-left: n(27);
          }
        }
        .rightArrow{
          margin-right: n(27);
          width: n(20);
          height: n(40);
        }
      }
    }
    .userMain{
      width: 90%;
      margin: auto;
      ul{
        li{
          margin-top: n(20);
          display: inline-flex;
          justify-content: space-between;
          align-items: center;
          width: 100%;
          height: n(70);
          background: #ffffff;
          -webkit-border-radius: 10px;
          -moz-border-radius: 10px;
          border-radius: 10px;
          p{
            margin-left: n(27);
            display: inline-flex;
            justify-content: center;
            align-items: center;
            img{
              width: (50);
              height: n(50);
            }
            a{
              margin-left: n(20);
            }
          }
          .rightArrow{
            margin-right: n(27);
            width: n(20);
            height: n(40);
          }
        }
      }
    }
    .exit{
      letter-spacing: 2px;
      margin-top: n(30);
      width: 100%;
      height: n(90);
      text-align: center;
      line-height: n(90);
      background: #ffffff;
      font-size: n(28);
    }
  }
</style>



